export function setName(name) {
  return {
    type: 'APP_SET_NAME',
    name: name
  };
}

export function loadingOn() {
  return {
    type: 'LOADING',
    loading: true
  };
}

export function loadingOff() {
  return {
    type: 'LOADING',
    loading: false
  };
}



